.filepicker-container .drawer-header .form-group {
  position: relative;
}

.filepicker-container .drawer-header .form-group .spinner-border {
  position: absolute;
  left: 20px;
  top: 50%;
  margin-top: -0.5rem;
  z-index: 10;
}

.filepicker-container .drawer-header .form-group .form-control {
  padding-right: 2.5rem;
}

.filepicker-container .drawer-header form {
  position: relative;
}

.filepicker-container .drawer-header form label {
  margin: 0;
}

.filepicker-container .drawer-header form input[type=file] {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  left: 0;
  top: 0;
}

.filepicker-container .drawer-body {
  padding-left: 0;
  padding-right: 0;
  padding-top: .5rem;
  padding-bottom: .5rem;
}

.filepicker-container .drawer-body .filepicker-item {
  margin: 0;
  padding: .5rem 1rem;
}

.filepicker-container .drawer-body .filepicker-item.filepicker-item-error, .filepicker-container .drawer-body .filepicker-item.filepicker-item-selectable {
  cursor: pointer;
}

.filepicker-container .drawer-body .filepicker-item.filepicker-item-error:hover, .filepicker-container .drawer-body .filepicker-item.filepicker-item-selectable:hover {
  background-color: #dee2e6;
}

.filepicker-container .drawer-body .filepicker-item.filepicker-item-active {
  background-color: #e9ecef;
}

.filepicker-container .drawer-body .filepicker-item.filepicker-item-uploader .progress {
  margin-top: .3rem;
  height: .5rem;
}

.filepicker-container .drawer-body .filepicker-item.filepicker-item-error {
  transition: opacity .3s ease;
  opacity: 1;
}

.filepicker-container .drawer-body .filepicker-item > img {
  margin-right: 1rem;
}

.filepicker-container .drawer-body .filepicker-item .media-body {
  line-height: 1rem;
  min-width: 1px;
}

.filepicker-container .drawer-body .filepicker-item .media-body > h6, .filepicker-container .drawer-body .filepicker-item .media-body small {
  margin: 0;
  display: block;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}

